import { useState, useEffect } from "react";
import Item from "./Item";
import { TextField, Button, Paper, List, Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { useForm } from "./useForm";

const Items = () => {
    const [values, handleChange] = useForm({
        name: "",
        description: "",
        link: "",
        price: "",
    });

    const [password, setPassword] = useState("");
    const [admin, setAdmin] = useState(false);
    const [spoiler, setSpoiler] = useState(false);
    const [items, setItems] = useState("");

    const useStyles = makeStyles((theme) => ({
        root: {
            "& .MuiTextField-root": {
                margin: theme.spacing(1),
                width: "10ch",
            },
            "& .MuiButton-root": {
                margin: theme.spacing(1),
                width: "25ch",
            },
        },
        body: {
            margin: "auto",
        },
    }));

    const classes = useStyles();

    const getData = () => {
        fetch(
            "https://wishlist-40fdd-default-rtdb.firebaseio.com/wishlist.json"
        )
            .then((res) => res.json())
            .then((res) => setItems(res));
    };

    const data = {
        name: values.name,
        description: values.description,
        link: values.link,
        price: values.price,
    };

    const addData = (e) => {
        e.preventDefault();
        if (!admin) {
            return;
        }

        const requestOptions = {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify(data),
        };
        fetch(
            "https://wishlist-40fdd-default-rtdb.firebaseio.com/wishlist.json",
            requestOptions
        )
            .then((response) => response.json())
            .then(
                setTimeout(function () {
                    getData();
                }, 500)
            );
    };

    const deleteData = (key, admin) => {
        if (!admin) {
            return;
        }
        const requestOptions = {
            method: "DELETE",
            headers: { "Content-Type": "application/json" },
        };
        fetch(
            `https://wishlist-40fdd-default-rtdb.firebaseio.com/wishlist/${key}.json`,
            requestOptions
        )
            .then((response) => response.json())
            .then(
                setTimeout(function () {
                    getData();
                }, 500)
            );
    };

    const reserveItem = (key) => {
        const requestOptions = {
            method: "PATCH",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({
                bought: true,
            }),
        };
        fetch(
            `https://wishlist-40fdd-default-rtdb.firebaseio.com/wishlist/${key}.json`,
            requestOptions
        )
            .then((response) => response.json())
            .then(
                setTimeout(function () {
                    getData();
                }, 500)
            );
    };

    useEffect(() => {
        getData();
    }, []);

    useEffect(() => {
        setAdmin(password === "barettjones");
    }, [password]);

    useEffect(() => {
        setSpoiler(false);
    }, [admin]);

    return (
        <div className={classes.body}>
            <Paper elevation={3} className={classes.paper}>
                <List>
                    {items && Object.keys(items).length !== 0
                        ? Object.keys(items).map((key, index) => {
                              return (
                                  <Item
                                      key={key}
                                      itemkey={key}
                                      name={items[key].name}
                                      description={items[key].description}
                                      link={items[key].link}
                                      price={items[key].price}
                                      bought={items[key].bought}
                                      deleteClick={deleteData}
                                      reserve={reserveItem}
                                      auth={admin}
                                      spoiler={spoiler}
                                  />
                              );
                          })
                        : "No Data"}
                </List>

                <form className={classes.root} noValidate autoComplete="off">
                    <Paper
                        elevation={3}
                        style={{
                            display: "flex",
                            justifyContent: "center",
                            alignItems: "center",
                        }}
                    >
                        {admin && (
                            <>
                                <TextField
                                    label="Name"
                                    name="name"
                                    value={values.name}
                                    onChange={handleChange}
                                />
                                <TextField
                                    label="Description"
                                    name="description"
                                    value={values.description}
                                    onChange={handleChange}
                                />
                                <TextField
                                    label="Link"
                                    name="link"
                                    value={values.link}
                                    onChange={handleChange}
                                />
                                <TextField
                                    label="Price"
                                    name="price"
                                    value={values.price}
                                    onChange={handleChange}
                                />
                                <Button variant="contained" onClick={addData}>
                                    Add item
                                </Button>
                            </>
                        )}
                        {!admin && (
                            <>
                                <Grid
                                    container
                                    alignItems="center"
                                    justify="center"
                                    direction="column"
                                >
                                    <Grid
                                        container
                                        item
                                        xs={12}
                                        alignItems="center"
                                        justify="center"
                                        direction="column"
                                    >
                                        <Button
                                            variant="contained"
                                            onClick={() => setSpoiler(!spoiler)}
                                        >
                                            {spoiler
                                                ? "Hide Bought Items"
                                                : "Show Bought Items"}
                                        </Button>
                                    </Grid>
                                    <Grid
                                        container
                                        item
                                        xs={12}
                                        alignItems="center"
                                        justify="center"
                                        direction="column"
                                    >
                                        <TextField
                                            label="Password"
                                            type="password"
                                            name="password"
                                            value={password}
                                            onChange={(e) =>
                                                setPassword(e.target.value)
                                            }
                                        />
                                    </Grid>
                                </Grid>
                            </>
                        )}
                    </Paper>
                </form>
            </Paper>
        </div>
    );
};

export default Items;
